import React, { Fragment } from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import { Typography } from '@material-ui/core'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import CircularProgress from '@material-ui/core/CircularProgress'
import firebase from '../firebase'

const styles = theme => ({
  container: {
    position: 'relative',
    top: 60,
    flexWrap: 'wrap',
    margin: 16,
    backgroundColor: '#FFFFFF',
    padding: 16,
    borderRadius: 6
  },
  textField: {
    width: '100%',
  },
  button: {
    margin: '20px 0 0',
  },
  rightIcon: {
    marginLeft: theme.spacing.unit,
  },
  btnText: {
    color: '#FFFFFF'
  },
  progress: {
    display: 'block',
    margin: '16px auto'
  }
})

class Main extends React.Component {

  state = {
    open: false,
    loading: true,
    text: ''
  }

  handleClose = () => {
    this.setState({ open: false })
  }

  sendMessage = () => {
    this.setState({ loading: false, text : "Mensagem enviada com sucesso!" })
    this.title.value = ""
    this.content.value = ""
  }

  handleSend = () => {
    this.setState({ open: true, loading: true })
    let newMessage = {
      title: this.title.value,
      content: this.content.value,
      date: Date.now()
    }

    if (newMessage.title === "" || newMessage.content === "") {
      this.setState({ loading: false, text : "Preencher 'Título' e 'Texto' da mensagem" })
      return false
    } else {
      // setTimeout(this.sendMessage, 1000) // Teste local
      firebase.push('messages', {
        data: newMessage
      }).then(data => {
        this.setState({ loading: false, text : "Mensagem enviada com sucesso!" })
        this.title.value = ""
        this.content.value = ""
      }).catch(err => {
        console.log(err)
      })
    }
    
  }

  render() {
    const { classes } = this.props

    return (
      <Fragment>
        <form className={classes.container} noValidate autoComplete="off">
          <TextField
            id="standard-with-placeholder"
            label="Título da mensagem"
            placeholder="Título da mensagem"
            className={classes.textField}
            margin="normal"
            inputRef={el => this.title = el}
          />
          <TextField
            id="standard-multiline-static"
            label="Texto da mensagem"
            multiline
            rows="4"
            className={classes.textField}
            margin="normal"
            inputRef={el => this.content = el}
          />
          <Button variant="contained" color="primary" className={classes.button} onClick={this.handleSend}>
            <Typography className={classes.btnText}>Enviar</Typography>
          </Button>
        </form>
        <Dialog aria-labelledby="simple-dialog-title" open={this.state.open} onClose={this.handleClose}>
          <DialogTitle id="simple-dialog-title">
            { this.state.loading ? 'Enviando...' : 'Aviso' }
          </DialogTitle>
          <DialogContent>
            { this.state.loading ? 
              <CircularProgress className={classes.progress} /> : 
              <DialogContentText id="alert-dialog-slide-description">{this.state.text}</DialogContentText>
            }
          </DialogContent>
          <DialogActions>
            { !this.state.loading && <Button onClick={this.handleClose} color="primary">OK</Button> }
          </DialogActions>
        </Dialog>
      </Fragment>

    )
  }
}

Main.propTypes = {
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(Main)